    initialize() {
        this.logger.debug(LOG_SOURCE, LOG_CATEGORY, 'initialize');
        const requestId = this.generateRequestId('General');
        const task = new WorkerTask(this.worker, requestId);
        const request = {
            id: requestId,
            type: 'ExecuteRequest',
            data: {
                name: 'initialize',
                args: [],
            },
        };
        this.proxy(task, request);
        return task;
    }
